@import "variables.less";

dl.image-tags {
    dt {
        line-height: 30px;
    }

    .image-tag {
        padding: 2px 5px;
        line-height: 30px;
    }

    .image-tag:after {
        content: '\a';
        white-space: pre;
    }
}

pre {
    font-size: 13px;
    padding: 5px 10px;
    background-color: @command-bg-color;
    border: none;
    font-family: Monospace, Menlo, Consolas;

    em, span.placeholder {
        font-weight: bold;
        font-style: normal;
    }
}

.fa-info-circle {
    font-size: 15px;
    color: @metadata-color;
    padding: 0px 5px;
}

.image-metadata-layers {
    padding-top: 15px;
    clear: both;

    p {
        margin-bottom: 3px;
    }
}



